import { TokenParams } from './../_models/token-model/TokenParams';
import { Injectable } from '@angular/core';
import { tokenNotExpired } from 'angular2-jwt';
import { Observable, of } from 'rxjs';
import { tap, delay } from 'rxjs/operators';
import { HttpClientModule } from '@angular/common/http';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { environment } from '../../../environments/environment';

@Injectable()
export class AuthService {
  isLoggedIn = false;
  baseUrl = environment.baseUrl;

  constructor( public httpClient: HttpClient) {
  }
  // store the URL so we can redirect after logging in
  redirectUrl: string;

  // store the userName and passoword
  userName: string;
  password: string;

  login(): Observable<any> {

    // tslint:disable-next-line:prefer-const
    let data = {'username': this.userName ,
                'password': this.password ,
                'grant_type': 'password',
                'refresh_token': ' ',
                'client_id' : '100',
                'client_secret' : '888',
              };
    // tslint:disable-next-line:prefer-const
    let reqHeader = new HttpHeaders({ 'Content-Type': 'application/json'});
     // return this.httpClient.post(this.baseUrl + '/token/auth', data, { headers: reqHeader });
     return this.httpClient.post(this.baseUrl + '/token/auth', data, { headers: reqHeader });
    // return of(true).pipe(
    //   tap(val => (this.userName === 'admin' && this.password === 'admin') ? this.isLoggedIn = true :
    //   this.isLoggedIn = false )
    // );
  }

  logout(): void {
    this.userName = '';
    this.password = '';
    this.isLoggedIn = false;
  }

  public getToken(): string {
    return localStorage.getItem('token');
  }

  public isAuthenticated(): boolean {
    // get the token
    const token = this.getToken();
    // return a boolean reflecting
    // whether or not the token is expired
    return tokenNotExpired(token);
  }

}
