import { AuthService } from './../_shared/_auth/auth.service';
import { RouterModule } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup } from '@angular/forms';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent  {

  messageValid: string;
  loginForm: FormGroup;

  constructor(public authService: AuthService, public router: Router, public fb: FormBuilder) {

    this.createForm();
  }



  createForm() {
    this.loginForm = this.fb.group({
      userName: '', // <--- the FormControl called "name"
      password: ''
    });
  }

  login() {

    // console.log('form value'+ JSON.stringify(this.loginForm.value) + this.loginForm.valid);
    this.authService.userName = this.loginForm.value.userName;
    this.authService.password = this.loginForm.value.password;
    this.authService.login().subscribe((data) => {

      console.log('after Token'+ JSON.stringify(data));
      if (this.authService.isLoggedIn) {
        // Get the redirect URL from our auth service
        // If no redirect has been set, use the default
        // tslint:disable-next-line:prefer-const
        let redirect = this.authService.redirectUrl ? this.authService.redirectUrl : '/admin';

        // Redirect the user
        this.router.navigate([redirect]);
      } else {
      this.messageValid = 'Incorrect UserName OR Password';

      }


    });
  }

  logout() {
    this.authService.logout();
    
  }

}
